.CompactCard{
    display: flex;
    flex: 1;
    height: 7rem!important;
    border-radius: 0.7rem;
    color: white;
    position: relative;
    cursor: pointer;
    padding: 1rem;
}

.CompactCard:hover{
    box-shadow: none;
}

.radialBar{
    display: flex;
    flex: 1;
    justify-content: flex-end;
    flex-direction: column ;
    gap: 1rem;
}
.CircularProgressbar{
    width: 4rem!important;
    overflow: visible!important;

}
.CircularProgressbar-path{
    stroke: white!important;
    stroke-width: 12px!important;
    filter: drop-shadow(2px 4px 6px white);
}
.CircularProgressbar-trail{
   display: none;
}
.CircularProgressbar-text{
    fill: white!important;
}
.radialBar span{
    font-size: 17px;
    font-weight: bold;
}

/* detail */
.detail{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: 1;
    align-items: flex-end;
}
.detail span:nth-child(2){
    font-size: 22px;
    font-weight: bold;
}
.detail span:nth-child(3){
    font-size: 12px;
}
/* Expanded Card */
.ExpandedCard{
    position: absolute;
    width: 60%;
    height: 70vh;
    z-index: 9;
    left: 13rem;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
}
.ExpandedCard span:nth-of-type(1){
    color: white;
    font-size: 26px;
    font-weight: bold;
    text-shadow: 0px 0px 15px white;
}
.ExpandedCard span:nth-of-type(2){
    color: white;
    font-size: 16px;
}
.chartContainer{
    width: 70%;
}

@media screen and (max-width: 1200px){
    .ExpandedCard{
        top: 2rem;
        left: 6rem;
        height: 70vh;
    }
}

@media screen and (max-width: 768px){
    .ExpandedCard{
        top: 8rem;
        height: 50%;
        left: 15px;
        width: 80%;
    }
}
